.gray_box {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #D9D9D9;
  z-index: 1;
  border-radius: 40px;
  display: flex; 
  justify-content: center; 
  align-items: center; 
}

.square {
  width: 97%;
  height: 95%;
  background-color: #F0F0F0;
  z-index: 2;
  border-radius: 40px;
  flex-direction: column;
}
